import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import Typewriter from "typewriter-effect"
import NavigationBar from "../components/navigationBar"

const AboutPage = () => (
  <Layout>
    <SEO title="About Me" />
    <NavigationBar active="about" color="#22818b" />
    <div className="description">
      <Typewriter
        onInit={typewriter => {
          typewriter
            .changeDeleteSpeed(2)
            .changeDelay(30)
            .pauseFor(300)
            .typeString("Come on in,")
            .pauseFor(400)
            .typeString(" let's get acquainted.")
            .start()
        }}
      />
    </div>
  </Layout>
)

export default AboutPage
